.date-range input{
  width: 12vw;
}

/*Here is an example on animations*/
@media (prefers-reduced-motion: no-preference) {
  .dontuse {
    animation: custom-spin infinite 20s linear;
  }
}

@keyframes custom-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
